import React,{useEffect, useRef, useState} from 'react'
import * as faceapi from 'face-api.js';
import { Stream } from '@mui/icons-material';
import { TinyFaceDetectorOptions } from 'face-api.js';

function FaceRecognition() {
  const [initialization, setinitialization] = useState(false);
  const videoRef = useRef();
  const canvasRef = useRef();
  const videWidth = 480;
  const videoHeight = 640;

  useEffect(() => {
 const loadModels =async ()=>{
  const MODEL_URL = process.env.PUBLIC_URL+'/models'
  setinitialization(true);
  Promise.all([
    faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL),
    faceapi.nets.faceLandmark68Net.loadFromUri(MODEL_URL),
    faceapi.nets.faceRecognitionNet.loadFromUri(MODEL_URL),
    faceapi.nets.faceExpressionNet.loadFromUri(MODEL_URL),

  ]).then(startVideo);
 
 }
 loadModels();
  }, []);

  const startVideo = ()=>{
    navigator.getUserMedia({
      video:{}
    },stream=>videoRef.current.srcObject= stream,
   err=> console.error(err))
  } 

  const HandleVideoOnPlay=()=>{
    setInterval(async()=>{
      if(initialization){
        setinitialization(false)
      }

      canvasRef.current.faceapi.createCanvas(videoRef.current);
      const displatSize = {
        width:videWidth,
        height:videoHeight
      }
      faceapi.matchDimensions(canvasRef.current,displatSize);
      const detection = await faceapi.detectAllFaces(videoRef.current,new TinyFaceDetectorOptions).withFaceLandmarks().withFaceExpressions();
      const resizedDetection = faceapi.resizeResults(detection,displatSize);
      canvasRef.current.getContext('2d').clearRect(0,0,videWidth,videoHeight);
      faceapi.draw.drawDetections(canvasRef.current,detection);
      faceapi.draw.drawFaceLandmarks(canvasRef.current,detection);
      faceapi.draw.drawFaceExpressions(canvasRef.current,detection);


      console.log(detection)
    },100)

  }

  return (
    <div className='App'>
      <span>{initialization?"Wait...":"Model loaded"}</span>
      <div className='display-flex justify-content-center'>
      <video autoPlay muted ref={videoRef} height={videoHeight} width={videWidth} onPlay={HandleVideoOnPlay}/>
      <canvas ref={canvasRef} className='position-absolute'/>
      </div>
    </div>
  )
}

export default FaceRecognition
